<template>
  <div class="height">
        <van-nav-bar title="选择身高" left-text="返回" left-arrow @click-left="$router.go(-1)"/>


        <!-- <div class="wrap" v-if="choose.id">
            <div class="tag_wrap">
                    <div class="scr">
                        <div class="tag">
                            <p> {{choose.name}} </p>
                        </div>
                    </div>
                    <div class="clear" @click="clear">
                        <p>清除</p>
                        <van-icon name="cross"/>
                    </div>
            </div>
        </div> -->


        <div class="choose_wrap">
            <div  v-if="s_sex.status == 'm'">
                <div class="item" v-for="(item, index) in list_m" :key="item.id" @click="chooseItem_m(item, index)">
                    <p :class="item.active ? 'active' : ''">{{item.name}}</p>
                </div>
            </div>
            <div v-if="s_sex.status == 'f'">
                <div class="item" v-for="(item, index) in list_f" :key="item.id" @click="chooseItem_f(item, index)">
                    <p :class="item.active ? 'active' : ''">{{item.name}}</p>
                </div>
            </div>
        </div>

        <div class="btn-wrap">
            <van-button type="primary" size="large" color="#2873ff" @click="submit">确定</van-button>
        </div>
  </div>
</template>
 
<script>
import { mapState, mapGetters } from "vuex";
export default {
    name: 'material',
    data () {
        return {
            choose: {
                name: '',
                id: ''
            },
            list_m: [
                {
                    name: '160-',
                    id: '1',
                    active: false
                },{
                    name: '161~165',
                    id: '2',
                    active: false
                },{
                    name: '166~170',
                    id: '3',
                    active: false
                },{
                    name: '171~175',
                    id: '4',
                    active: false
                },{
                    name: '176~180',
                    id: '5',
                    active: false
                },{
                    name: '181~185',
                    id: '6',
                    active: false
                },{
                    name: '186+',
                    id: '7',
                    active: false
                }
            ],
            list_f: [
                {
                    name: '150-',
                    id: '1',
                    active: false
                },{
                    name: '151~155',
                    id: '2',
                    active: false
                },{
                    name: '156~160',
                    id: '3',
                    active: false
                },{
                    name: '161~165',
                    id: '4',
                    active: false
                },{
                    name: '166~170',
                    id: '5',
                    active: false
                },{
                    name: '171~175',
                    id: '6',
                    active: false
                },{
                    name: '176+',
                    id: '7',
                    active: false
                }
            ]

        }
    },
    computed: {
        ...mapState({
            s_sex: state => state.s_sex,
            s_height: state => state.s_height
        })
    },
    created() {
        console.log( this.s_sex )
        console.log( this.s_height )
        let mArr = this.list_m;
        let fArr = this.list_f;

        mArr.forEach((item, index) => {
            item.active = false;
            if(item.id == this.s_height.m.id) {
                item.active = true;
            }
        })
        
        fArr.forEach((item, index) => {
            item.active = false;
            if(item.id == this.s_height.f.id) {
                item.active = true;
            }
        })

        this.list_m = mArr;
        this.list_f = fArr;
    },
    methods: {
        submit() {
            switch(this.s_sex.status) {
                case 'm':
                    console.log('男')
                    this.$store.state.s_height.m = this.choose;
                    break;
                case 'f':
                    console.log('女')
                    this.$store.state.s_height.f = this.choose;
                    break;
            }
            console.log( this.choose )
            this.$store.state.s_height.error = false;
            this.$store.state.s_height.err_msg = '';
            console.log(this.$store.state.s_height)
            this.$router.go(-1);
        },
        chooseItem_m(val, index) {
            this.choose = val;
            this.list_m.forEach( (item, _index) => {
                item.active = false;
                this.$set(this.list_m, _index, item);
            })
            val.active = true;
            // this.$set(this.list_m, index, val);
            // this.$store.state.s_height.m = val;
            console.log( val )
        },
        chooseItem_f(val, index) {
            this.choose = val;
            this.list_f.forEach( (item, _index) => {
                item.active = false;
                this.$set(this.list_f, _index, item);
            })
            val.active = true;
            // this.$set(this.list_f, index, val);
            // this.$store.state.s_height.f = val;
            console.log( val )
        },
        clear() {
            this.choose = {
                name: '',
                id: ''
            }
        }
  }
}
</script>
 
<style scoped lang ="stylus">
.height
    .wrap
        .tag_wrap
            position relative
            display flex
            background #F8F8F8
            padding 12px
            .tag
                display flex
                align-items center
                background #FFF
                padding 4px
                border-radius 2px
            .clear
                position absolute
                right 12px
                display flex
                align-items center
                top 12px
                padding 4px
                color #2873fe
    .choose_wrap
        padding 8px
        .item 
            display inline-block
            width 25%
            box-sizing border-box
            padding 4px
            p 
                background #F8F8F8
                text-align center
                padding 12px 0
                color #333
                width 100%
            .active
                color #2873fe
                background rgba(40,115,255,0.1)
    .btn-wrap
        position fixed
        left 0
        bottom 0
        width 100%

</style>